<template>
  <div class="hs-container">
    <c-header :title="$t('base.Refund')" isBackNav></c-header>
    <hs-scroll
      class="table-view"
      ref="tableView"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getData">
      <ul>
        <div class="warning">{{$t('base.ReminderPassengersWhoHaveCheckedInMustFirstCancelTheCheckIn')}}</div>
        <hs-section v-if="details.Segments && !noValue(JSON.stringify(details.Segments))">
          <div v-for="(item, index) in details.Segments" :key="index" style="width: 100%">
            <hs-cell-1 :showLogo="false">
              <div class="travel-details">
                <div class="time">
                  <div class="mark">{{_showSegmentTitle(index)}}</div>
                  <p>{{beTime(item.DepartDate, 'MM月dd日')}} {{getWeeks(beTime(item.DepartDate))}} {{item.DepartCityName}}&nbsp;-&nbsp;{{item.ArrivalCityName}}</p>
                </div>
                <div class="plane">
                  <div class="logo" :style="{'background-image': `url(${item.AirLineLogoUrl})`}"></div>
                  <p>{{item.AirLineName}}<template>&nbsp;<span v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.CodeShare')}}</span></template>&nbsp;{{item.FlightNo}}<template v-if="!noValue(item.Plane)">&nbsp;|&nbsp;{{item.Plane}}</template><template v-if="!noValue(item.Meal)">&nbsp;|&nbsp;{{item.Meal}}</template>
                  </p>
                </div>
                <div class="plane">
                  <p v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.OperatedBy')}}&nbsp;{{item.ActualCarryFlightNo}}</p>
                </div>
                <div class="details">
                  <div class="source">
                    <p>{{beTime(item.DepartDate, 'hh:mm')}}</p>
                    <p>{{item.DepartAirportName}} {{item.DepartTerminal}}</p>
                  </div>
                  <div class="to">
                    <div class="stopOver-logo" v-if="item.StopOver">
                      <p>{{$t('base.By')}}</p>
                    </div>
                    <div class="logo" v-else></div>
                    <div class="dashed"></div>
                    <p class="stopOver" v-if="item.StopOver">{{item.StopOver.StopCity}}</p>
                  </div>
                  <div class="destination">
                    <p>{{beTime(item.ArrivalDate, 'hh:mm')}}<span v-if="item.AddDays > 0">+{{item.AddDays}}</span></p>
                    <p>{{item.ArrivalAirportName}} {{item.ArrivalTerminal}}</p>
                  </div>
                </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" @click.native="_getFlightCabinRule(item.Rule)">
              <div class="cost-details">
                <div class="details">
                  <div><span>{{$t('base.AirTickets')}}</span><span>￥{{item.SalePrice ? item.SalePrice : 0}}</span></div>
                  <div>
                    <span>{{$t('base.PlaneConstruction')}}&nbsp;/&nbsp;{{$t('base.Fuel')}}</span>
                    <span>
                      ￥{{
                        (item.FuelOilTax ? item.FuelOilTax : 0) +
                        (item.AirportTax ? item.AirportTax : 0)
                      }}
                    </span>
                  </div>
                </div>
                <p>{{$t('base.TicketRestrictions')}} ></p>
              </div>
            </hs-cell-1>
          </div>
        </hs-section>
        <hs-section>
          <hs-cell-1 :value="$t('base.ChoosePassengersWhoNeedToRefund')" :showLogo="false"></hs-cell-1>
          <hs-cell-1 :showLogo="false" v-for="(item, index) in details.Passengers" :key="index" v-if="item.CanRefund">
            <div class="refund-passenger">
              <div
                class="header" @click="details.Passengers[index].isOpen = !details.Passengers[index].isOpen"
                :style="{'border-bottom-width': details.Passengers[index].isOpen ? '1px' : '0px'}">
                <!-- :style="{'border-bottom-width': details.Passengers[index].isOpen ? '1px' : '0px'}"> -->
                <div>
                  <div class="logo" @click.stop="_togglePassengerSelect(index)" v-if="item.isSelected" style="background-image: url('static/image/base/check-box/check-box.png')"></div>
                  <div class="logo" @click.stop="_togglePassengerSelect(index)" v-else style="background-image: url('static/image/base/check-box/check-box-default.png')"></div>
                  <p @click.stop="_togglePassengerSelect(index)">{{item.Name}}</p>
                </div>
                <div class="arrow" :style="{transform: `rotate(${details.Passengers[index].isOpen ? 90 : -90}deg)`}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
              </div>
              <div class="notice">
                <div class="refundPrice">
                  <p>{{$t('base.RefundFee')}}</p>
                  <div class="logo" style="background-image: url('static/image/plane-ticket/details/zhushi.png')" @click="showAlert = true"></div>
                </div>
                <p>
                  <span v-if="details.RefundType === 1">￥0</span>
                  <span v-else-if="item.RefundPrice > 0">￥{{item.RefundPrice}}</span>
                  <span v-else>{{$t('base.AirlinePassengerMaintenance')}}</span>
                </p>
              </div>
              <template v-if="details.Passengers[index].isOpen">
                <div class="notice">
                  <p>{{item.CredentialName}}</p>
                  <p>{{item.CredentialNo}}</p>
                </div>
                <div class="notice">
                  <p>{{$t('base.Email')}}</p>
                  <p>{{item.Email}}</p>
                </div>
                <div class="notice">
                  <p>{{$t('base.CellphoneNumber')}}</p>
                  <p>{{item.Mobile}}</p>
                </div>
                <div class="notice">
                  <p>{{$t('base.TicketNumber')}}</p>
                  <p>{{item.OriginTicketNo}}</p>
                </div>
                <div class="notice">
                  <p>{{$t('base.WhetherToSendARefundSuccessfulSMS')}}</p>
                  <hs-switch v-model="details.Passengers[index].IsSendRefundSms"></hs-switch>
                </div>
                <div class="notice">
                  <p>{{$t('base.WhetherToSendARefundSuccessfulMail')}}</p>
                  <hs-switch v-model="details.Passengers[index].IsSendRefundEmail"></hs-switch>
                </div>
                <!-- <div class="notice">
                  <p>是否发送退票成功微信推送</p>
                  <hs-switch v-model="details.Passengers[index].IsSendRefundWechat"></hs-switch>
                </div> -->
              </template>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 选择退票原因 -->
        <hs-section>
          <hs-cell-1 value="选择退票原因" :showLogo="false"></hs-cell-1>
          <hs-cell-1 :showLogo="false" @click.native="details.RefundType = 0">
            <div class="custom-cell-reason">
              <div class="header">
                <p>{{$t('base.VoluntaryRefund')}}</p>
                <div v-show="details.RefundType === 0" class="logo" style="background-image: url('static/image/base/check-box/single.png')"></div>
                <div v-show="details.RefundType === 1" class="logo" style="background-image: url('static/image/base/check-box/single-default.png')"></div>
              </div>
              <p class="des">{{$t('base.ChargeByAirlineRefundRules')}}</p>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" @click.native="details.RefundType = 1">
            <div class="custom-cell-reason">
              <div class="header">
                <p>{{$t('base.InvoluntaryRefund')}}</p>
                <div v-show="details.RefundType === 1" class="logo" style="background-image: url('static/image/base/check-box/single.png')"></div>
                <div v-show="details.RefundType === 0" class="logo" style="background-image: url('static/image/base/check-box/single-default.png')"></div>
              </div>
              <p class="des">{{$t('base.FlightCancellationDelayForMoreThan')}}</p>
            </div>
          </hs-cell-1>
        </hs-section>
        <!-- 选择退票原因 end -->

        <!-- 联系人 -->
        <hs-section v-if="details.DefaultContact">
          <hs-cell-1 :value="$t('base.ContactInformation')" logoName="add.png" @click.native="$refs.CFrequentContacts.open()"></hs-cell-1>
          <hs-cell-1 v-for="(item, index) in details.ContactList" :key="index">
            <div class="passenger-infor" :style="{transform: `translateX(${!item.openDelete ? '0px' : '-65px'})`}">
              <div class="left" @click="_setContactsOpenDelete(true, index)">
                <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
              </div>
              <div class="right" @click="_gotoContactsDetails(index)">
                <div class="item">
                  <p class="name">{{item.Name}}</p>
                </div>
                <div class="item">
                  <p>{{$t('base.CellphoneNumber')}}</p>
                  <p>{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                </div>
                <div class="item">
                  <p>{{$t('base.Email')}}</p>
                  <p>{{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
                <div class="delete" @click.stop="_deleteContact(index)">{{$t('base.Delete')}}</div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <!-- 联系人 end -->

      </ul>
    </hs-scroll>
    <div class="footer">
      <hs-button width="50%" height="40px" @click="_gotoRefund">{{$t('base.Confirm')}}</hs-button>
    </div>
    <c-return-explain v-model="showExplain" :details="returnExplainDetails"></c-return-explain>
    <!-- <contacts v-model="showCContacts" :contact="contactDetails" @delegate="_contactsDelegate"></contacts> -->
    <c-contacts :show.sync="showCContacts" v-model="contactDetails" :datalist="details.ContactList" :OrderBusinessType="101" @delegate="_contactsDelegate"></c-contacts>
    <c-frequent-contacts ref="CFrequentContacts" :orderInit.sync="details" :datalist="details.ContactList" :OrderBusinessType="101" @delegate="_delegateWithFrequentContacts"></c-frequent-contacts>
    <hs-alert :show.sync="showAlert" :title="$t('base.Tips')" :content="$t('base.TheRefundFeeIsSubjectToTheReviewOfTheAirlineCompany')" cancelBtn="" @confirm="showAlert = false" maskClose></hs-alert>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsButton from 'base/hs-button/hs-button'
import HsSwitch from 'base/hs-switch/hs-switch'
import HsAlert from 'base/hs-alert/hs-alert'
// import Contacts from './contacts'
import CContacts from 'components/c-passenger/c-contacts'
import CReturnExplain from 'components/c-return-explain/c-return-explain'
import CFrequentContacts from 'components/c-passenger/c-frequent-contacts'
import eventBus from 'common/js/eventBus.js'
import {kpiFlightGetFlightRefundInitInfo, kpiFlightSaveRefundOrder} from 'api'
export default {
  beforeRouteLeave (to, from, next) {
    if (this.showCContacts) {
      this.showCContacts = false
      next(false)
    } else {
      next()
    }
  },
  created() {
    this._getData()
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {},
      showExplain: false,
      returnExplainDetails: {},
      showCContacts: false,
      contactDetails: {},
      showAlert: false
    }
  },
  methods: {
    _getData() {
      let params = {
        ItktID: this.$route.query.ItktID,
        PnrID: this.$route.query.PnrID
      }
      kpiFlightGetFlightRefundInitInfo(params).then((res) => {
        console.log('退票初始化：', res)
        this.details = res.ResultData
        this.details.Passengers.forEach((element, index) => {
          // 乘客
          this.$set(this.details.Passengers[index], 'isOpen', false)
          this.$set(this.details.Passengers[index], 'isSelected', false)
          this.$set(this.details.Passengers[index], 'IsSendRefundSms', true)
          this.$set(this.details.Passengers[index], 'IsSendRefundEmail', true)
          this.$set(this.details.Passengers[index], 'IsSendRefundWechat', true)
        })
        this.$set(this.details.DefaultContact, 'isOpen', true)
        this.$set(this.details.DefaultContact, 'IsSendRefundSms', true)
        this.$set(this.details.DefaultContact, 'IsSendRefundEmail', true)
        this.$set(this.details.DefaultContact, 'IsSendRefundWechat', true)
        this.$set(this.details.DefaultContact, 'openDelete', false)
        this.$set(this.details, 'ContactList', [this.details.DefaultContact])
        this.$set(this.details, 'TicketList', [])
        this.$set(this.details, 'RefundType', 0)
        if (this.details.Passengers && this.details.Passengers.length === 1) {
          this._togglePassengerSelect(0)
        }
      })
    },
    // 根据航段显示单程/往返程/N程
    _showSegmentTitle(index) {
      let length = this.details.Segments && !this.noValue(JSON.stringify(this.details.Segments)) ? this.details.Segments.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.details.Segments[0].DepartCityName === this.details.Segments[1].ArrivalCityName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },
    // 退改签信息
    _getFlightCabinRule(rule) {
      this.showExplain = true
      this.returnExplainDetails = {
        vhtml: rule
      }
    },
    // 选择/取消选择退票乘客
    _togglePassengerSelect(index) {
      this.details.Passengers[index].isSelected = !this.details.Passengers[index].isSelected
      this.details.Passengers[index].isOpen = this.details.Passengers[index].isSelected
      let TicketListIndex = this.details.TicketList.map(v => v.PassengerID).indexOf(this.details.Passengers[index].PsgID)
      if (this.details.Passengers[index].isSelected) {
        if (TicketListIndex < 0) {
          this.details.TicketList.push({
            PassengerID: this.details.Passengers[index].PsgID,
            Name: this.details.Passengers[index].Name,
            TicketNo: this.details.Passengers[index].OriginTicketNo,
            RefundPrice: this.details.Passengers[index].RefundPrice,
            ServicePrice: this.details.Passengers[index].ServicePrice ? this.details.Passengers[index].ServicePrice : 0,
            Mobile: this.details.Passengers[index].Mobile,
            Email: this.details.Passengers[index].Email,
            IsSendRefundSms: this.details.Passengers[index].PsgIsSendRefundSmsID,
            IsSendRefundEmail: this.details.Passengers[index].IsSendRefundEmail,
            IsSendRefundWechat: this.details.Passengers[index].IsSendRefundWechat
          })
        }
      } else {
        if (TicketListIndex >= 0) {
          this.details.TicketList.splice(TicketListIndex, 1)
        }
      }
    },
    // 添加联系人
    // _addContact() {
    //   this.contactDetails = this.details.ContactList.length > 0 ? {
    //     index: -1,
    //     Name: '',
    //     Mobile: '',
    //     Email: '',
    //     IsSendRefundSms: true,
    //     IsSendRefundEmail: true,
    //     IsSendRefundWechat: true,
    //     openDelete: false
    //   } : Object.assign(this.details.DefaultContact, {index: -1})
    //   this.showCContacts = true
    // },
    _setContactsOpenDelete(value, index) {
      this.details.ContactList[index].openDelete = value
    },
    _gotoContactsDetails(index) {
      if (this.details.ContactList[index].openDelete) {
        this._setContactsOpenDelete(false, index)
      } else {
        this.contactDetails = Object.assign(JSON.parse(JSON.stringify(this.details.ContactList[index])), {index: index})
        this.showCContacts = true
      }
    },
    _deleteContact(index) {
      this.details.ContactList.splice(index, 1)
    },
    _contactsDelegate(contacts) {
      this.details.ContactList.splice(contacts.index, 1, Object.assign(contacts, {openDelete: false}))
      // if (this.details.ContactList.map(v => v.Mobile).indexOf(item.Mobile) >= 0) {
      //   this.$toast('已存在相同手机号的联系人')
      // } else {
      //   if (item && item.index >= 0) {
      //     this.details.ContactList.splice(item.index, 1, Object.assign(item, {openDelete: false}))
      //   } else {
      //     this.details.ContactList.push(Object.assign(item, {openDelete: false}))
      //   }
      // }
    },
    _delegateWithFrequentContacts(val) {
      console.log('选中的联系人: ', val)
      this.details.ContactList = val
    },
    // ==================================================================
    // 去退票
    _gotoRefund() {
      console.log(this.details)
      if (this.noValue(this.details.TicketList) || this.details.TicketList.length === 0) {
        this.$toast(this.$t('TM.PleaseSelectThePassengerWhoNeedsToRefund'))
      } else if (this.noValue(this.details.ContactList) || this.details.ContactList.length === 0) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.Contacts')))
      } else {
        this._showTipDialog(this.$t('TM.WhetherToConfirmTheRefund'), this._confirmRefund)
      }
    },
    // 确认退票
    _confirmRefund() {
      let params = this.details
      kpiFlightSaveRefundOrder(params).then((res) => {
        console.log('退票成功： ', res)
        this.$toast(this.$t('TM.RefundSucceed'))
        setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
        this.$router.replace('/personal/refundList')
      })
    },
    _showTipDialog(tipContent, next, cancel, confirmBtn = this.$t('base.Confirm'), cancelBtn = this.$t('base.Cancel')) {
      this.dialog = this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: this.$t('base.Tips'),
        content: tipContent,
        confirmBtn: {
          text: confirmBtn,
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: cancelBtn,
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        onConfirm: next,
        onCancel: cancel
      })
      this.dialog.show()
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    HsButton,
    HsSwitch,
    HsAlert,
    CContacts,
    CReturnExplain,
    CFrequentContacts
  }
}
</script>
