              width calc(100% - 70px)
          >.name
            color $color_black_text_0 !important
            font-weight bold
            line-height 1.4em
            width: 100% !important
            margin-bottom 3px
          &:first-child
            color $color_black_text_0
        >.delete
          @extend .flex-center
          position absolute
          width 60px
          // height 100%
          background-color $color_theme
          color $color_white
          font-size 0.9rem
          top -11px
          right -100px
          bottom -11px
      &:last-child
        >.right
          >.delete
            bottom -10px

    // 选择退票原因
    .custom-cell-reason
      width 100%
      padding 15px 0
      >.header
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-bottom 5px
        >.logo
          width 24px
          height 24px
          background-size 100% 100%
      >.des
        width 100%
        font-size 0.8rem
        color $color_gray_text
  >.footer
    @extend .flex-row
    @extend .flex-center
    width 100%
    height 50px
    background-color $color_white
    border-top 1px solid $color_gray_layer
    padding 0 15px
</style>



